import React from "react";
import cssHeader from './Header.module.css';
import {NavLink} from "react-router-dom";

const Header = (props) => {
    return (
        <header className={cssHeader.header}>
            <img className={cssHeader.img}
                 src={"https://cdn2.iconfinder.com/data/icons/designer-skills/128/react-512.png"}
                alt={"headerImg"}></img>
            <div className={cssHeader.headerText}>fakebook</div>
            <div className={cssHeader.loginBlock}>
                {props.isAuth
                    ? <div>
                        <NavLink to={`/profile`} className={cssHeader.a__userLogin}>{props.login}</NavLink>
                        <NavLink to={"/users"}><button onClick={props.logoutThunkCreator}>logout</button></NavLink>
                      </div>
                    : <div>
                        <NavLink to={"/login"} className={cssHeader.a__userLogin}>Login</NavLink>
                        <NavLink to={"/signup"} className={cssHeader.a__userLogin}>Sign Up</NavLink>
                    </div>
                }
            </div>
        </header>
    )
};

export default Header;